.chart {
  display: inline-block;
  height: 140px;
  margin: 50px 0;
  position: relative;
  text-align: center;
  width: 140px;

  canvas {
    left: 0;
    position: absolute;
    top: 0;
  }
}

.percent {
  display: inline-block;
  font-size: 25px;
  font-weight: 300;
  line-height: 140px;
  z-index: 2;

  &:after {
    content: "%";
    font-size: 50%;
    margin-left: 0.1em;
  }
}
