@import "https://fonts.googleapis.com/css?family=Alegreya+Sans+SC";

#certificate h3 {
  font-size: 20px;
}

#certificate p {
  margin-bottom: 5px;
  font-size: 17px;
}

#certificate p a {
  font-weight: bold;
}

#certificate .pictures {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  width: 300px;
  margin: auto;
}

#certificate img {
  display: block;
  width: 100%;
  margin-bottom: 1em;
}

#certificate .full {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  padding: 8%;
  background: #CCC center no-repeat;
  background: rgba(0, 0, 0, 0.5) center no-repeat;
  background-size: contain;
  background-origin: content-box;
  z-index: 100;
}

#certificate a:focus + .full {
  display: block;
}

#footer {
  background-image: linear-gradient(-50deg, #db5773 10%, #0f218e 140%);
  text-align: center;
  padding: 40px 0;
  color: #FFF;
}

.copyright {
  margin: 0;
  font-family: archive;
}

.social-links {
  list-style: none;
  padding: 0;
  margin: 0 0 20px;
}

.social-links li {
  display: inline-block;
  margin: 5px;
}

.social-links a {
  width: 50px;
  height: 50px;
  display: block;
  line-height: 49px;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.6);
  -webkit-box-shadow: inset h v blur spread rgba(0, 0, 0, 0.5);
  box-shadow: inset h v blur spread rgba(0, 0, 0, 0.5);
  -webkit-border-radius: 25px;
  -moz-border-radius: 25px;
  border-radius: 25px;
  font-size: 20px;
  color: rgba(255, 255, 255, 0.6);
}

.social-links a:hover {
  border: 1px solid #ffffff;
  color: #ffffff;
}

body {
  font: 300 14px/1.8 '' Alegreya Sans SC '', sans-serif;
  color: #666;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: #E7746F;
  transition: all 0.125s ease-in-out 0s;
}

a:hover {
  text-decoration: none;
  color: #999;
}

span {
  color: #666;
}

h1 {
  font: 700 40px/1.2 archive, sans-serif;
  text-transform: uppercase;
  letter-spacing: 5px;
  margin: 5px 0;
  color: #222;
}

h2 {
  font: 700 40px/1.2 archive, sans-serif;
  text-transform: uppercase;
  letter-spacing: 5px;
  margin: 5px 0;
  color: #222;
  font-size: 30px;
  margin: 0 0 30px;
}

h3 {
  font: 700 40px/1.2 archive, sans-serif;
  text-transform: uppercase;
  letter-spacing: 5px;
  margin: 5px 0;
  color: #222;
  font-size: 13px;
  letter-spacing: 1px;
  margin: 0 0 5px;
}

h4 {
  font: 700 40px/1.2 archive, sans-serif;
  text-transform: uppercase;
  letter-spacing: 5px;
  margin: 5px 0;
  color: #222;
  font-size: 12px;
  letter-spacing: 1px;
}

h5 {
  font: 700 40px/1.2 archive, sans-serif;
  text-transform: uppercase;
  letter-spacing: 5px;
  margin: 5px 0;
  color: #222;
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: 400;
}

h6 {
  font: 700 40px/1.2 archive, sans-serif;
  text-transform: uppercase;
  letter-spacing: 5px;
  margin: 5px 0;
  color: #222;
  font-size: 11px;
  letter-spacing: 1px;
  font-weight: 400;
}

.pfblock {
  padding: 120px 0 100px;
}

.pfblock-gray {
  background: #f5f5f5;
}

.pfblock-image {
  padding: 0;
  color: #fff;
}

.pfblock-image h1 {
  color: #fff;
}

.pfblock-image h2 {
  color: #fff;
}

.pfblock-image h3 {
  color: #fff;
}

.pfblock-image h4 {
  color: #fff;
}

.pfblock-image h5 {
  color: #fff;
}

.pfblock-image h6 {
  color: #fff;
}

.pfblock-header {
  text-align: center;
  margin: 0 0 60px;
}

.pfblock-header-left {
  text-align: left;
}

.pfblock-header-left .pfblock-line {
  width: inherit;
  margin: 30px 0;
}

.pfblock-title {
  font-weight: 800;
}

.pfblock-subtitle {
  font-family: 'Alegreya Sans SC', "Times New Roman", Times, sans-serif;
  font-style: normal;
  font-size: 18px;
}

.pfblock-line {
  background: rgba(135, 135, 135, 0.5);
  width: 100px;
  height: 1px;
  margin: 30px auto;
}

.pfblock-icon {
  font-size: 32px;
}

.darkmode--activated #home {
  background-image: linear-gradient(80deg, #e2ede7 10%, #8df8e7 100%);
}

.darkmode--activated #footer {
  background-image: linear-gradient(80deg, #e2ede7 10%, #8df8e7 100%);
}

.darkmode--activated .pfblock {
  background: #e4e4e4;
}

.darkmode--activated .pfblock-gray {
  background: #e3e3e3;
}

.darkmode--activated .navbar-custom {
  background: #272727;
}

.darkmode--activated .navbar-custom .menu-item,
.darkmode--activated .navbar-custom .navbar-brand {
  color: #fff;
}

.darkmode--activated .navbar-custom .navbar-toggle .icon-bar {
  background: #fff;
}

.darkmode--activated h2,
.darkmode--activated h3 {
  color: #fff;
}

.darkmode--activated .percent,
.darkmode--activated .pfblock-subtitle,
.darkmode--activated p {
  color: #c9c9c9;
}

#home {
  background-image: linear-gradient(135deg, #FD6585 10%, #0D25B9 100%);
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
  padding: 0;
}

.home-overlay {
  background-color: rgba(44, 62, 80, 0.12);
  background-repeat: repeat;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 0;
}

.header {
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.04), 0 2px 1px -1px rgba(0, 0, 0, 0.01), 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  position: relative;
  width: 100%;
  z-index: 998;
}

.intro {
  position: absolute;
  width: 100%;
  top: 45.2%;
  left: 0;
  text-align: center;
  transform: translate(0%, -50%);
  padding: 0 15px;
}

.intro h1 {
  font-weight: 800;
  font-size: 50px;
}

.start {
  font-family: archive, "Times New Roman", Times, sans-serif;
  font-size: 23px;
  font-style: normal;
  text-transform: none;
  margin: 18px 0;
}

.navbar a {
  transition: all 0.4s ease-in-out 0s;
}

.navbar-custom {
  border: 0;
  border-radius: 0;
  margin: 0;
  text-transform: uppercase;
  font-family: archive, sans-serif;
  background: #fff;
  padding: 0;
}

.navbar-custom .navbar-brand {
  font-size: 15px;
  letter-spacing: 3px;
  color: #222;
  font-weight: 700;
  font-size: 22px;
  font-family: 'Alegreya Sans SC', cursive;
}

.navbar-custom .navbar-nav > li > a {
  font-size: 15px;
  letter-spacing: 3px;
  color: #222;
}

.navbar-custom .nav li.active {
  background: none;
  outline: 0;
  color: #d24a43;
}

.navbar-custom .nav li a:hover {
  background: none;
  outline: 0;
  color: #d24a43;
}

.navbar-custom .nav li a:focus {
  background: none;
  outline: 0;
  color: #d24a43;
}

.navbar-custom .navbar-toggle .icon-bar {
  background: #222;
}

@keyframes float {
  0% {
    transform: translatey(0px);
  }

  50% {
    transform: translatey(-5px);
  }

  100% {
    transform: translatey(0px);
  }
}

@-webkit-keyframes drop {
  0% {
    top: 0;
    opacity: 0;
  }

  30% {
    top: 10px;
    opacity: 1;
  }

  100% {
    top: 25px;
    opacity: 0;
  }
}

@keyframes drop {
  0% {
    top: 0;
    opacity: 0;
  }

  30% {
    top: 10px;
    opacity: 1;
  }

  100% {
    top: 25px;
    opacity: 0;
  }
}

.scroll-up {
  position: fixed;
  display: none;
  z-index: 999;
  bottom: 7em;
  right: 2em;
}

.scroll-up a {
  background-color: rgba(135, 135, 135, 0.5);
  display: block;
  width: 60px;
  height: 60px;
  border-radius: 30px;
  text-align: center;
  color: #fff;
  font-size: 20px;
  line-height: 60px;
}

.scroll-up a:hover {
  background-color: rgba(235, 235, 235, 0.8);
  color: #E7746F;
}

.scroll-up a:active {
  background-color: rgba(235, 235, 235, 0.8);
  color: #E7746F;
}

.scroll-down {
  position: absolute;
  left: 50%;
  bottom: 40px;
  border: 2px solid #fff;
  border-radius: 50%;
  height: 50px;
  width: 50px;
  margin-left: -15px;
  display: block;
  z-index: 10;
  text-align: center;
}

.scroll-down span {
  position: relative;
  color: #fff;
  animation-name: drop;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-play-state: running;
}

.chart {
  display: inline-block;
  height: 140px;
  margin: 50px 0;
  position: relative;
  text-align: center;
  width: 140px;
}

.chart canvas {
  left: 0;
  position: absolute;
  top: 0;
}

.percent {
  display: inline-block;
  font-size: 25px;
  font-weight: 300;
  line-height: 140px;
  z-index: 2;
}

.percent:after {
  content: "%";
  font-size: 50%;
  margin-left: 0.1em;
}

.grid {
  position: relative;
  clear: both;
  margin: 0 auto;
  padding: 1em 0 4em;
  max-width: 100%;
  list-style: none;
  text-align: center;
  max-width: 360px;
}

.grid h2 {
  font-size: 20px;
}

.grid figure {
  position: relative;
  float: left;
  overflow: hidden;
  margin: 10px 1%;
  min-width: 100%;
  max-width: 100%;
  height: auto;
  background: #3085a3;
  text-align: center;
  cursor: pointer;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.22), 0 6px 6px rgba(0, 0, 0, 0.19);
}

.grid figure img {
  position: relative;
  display: block;
  min-height: auto;
  max-width: 100%;
  opacity: 1;
}

.grid figure figcaption {
  padding: 2em;
  color: #fff;
  text-transform: uppercase;
  font-size: 1.25em;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.grid figure figcaption::before {
  pointer-events: none;
}

.grid figure figcaption::after {
  pointer-events: none;
}

.grid figure figcaption > a {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  text-indent: 200%;
  white-space: nowrap;
  font-size: 0;
  opacity: 0;
}

.grid figure h2 {
  word-spacing: -0.15em;
  font-weight: 300;
  opacity: 0;
  margin: 0;
}

.grid figure h2 span {
  font-weight: 800;
}

.grid figure p {
  opacity: 0;
  margin: 0;
  letter-spacing: 1px;
  font-size: 68.5%;
}

.grid figure p a {
  font-size: 13px;
}

figure.effect-bubba {
  background: #000;
}

figure.effect-bubba img {
  transition: opacity 0.35s;
}

figure.effect-bubba:hover img {
  opacity: 0.15;
}

figure.effect-bubba:hover figcaption::before {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

figure.effect-bubba:hover figcaption::after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

figure.effect-bubba:hover h2 {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

figure.effect-bubba:hover .webpage,
figure.effect-bubba:hover p {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

figure.effect-bubba figcaption::before {
  position: absolute;
  top: 30px;
  right: 30px;
  bottom: 30px;
  left: 30px;
  content: '';
  opacity: 0;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  -webkit-transform: scale(0, 1);
  transform: scale(0, 1);
}

figure.effect-bubba figcaption::after {
  position: absolute;
  top: 30px;
  right: 30px;
  bottom: 30px;
  left: 30px;
  content: '';
  opacity: 0;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  border-right: 1px solid #fff;
  border-left: 1px solid #fff;
  -webkit-transform: scale(1, 0);
  transform: scale(1, 0);
}

figure.effect-bubba h2 {
  padding-top: 10%;
  -webkit-transition: -webkit-transform 0.35s;
  transition: transform 0.35s;
  -webkit-transform: translate3d(0, -20px, 0);
  transform: translate3d(0, -20px, 0);
  color: #fff;
}

figure.effect-bubba .webpage,
figure.effect-bubba p {
  padding: 20px 2.5em;
  opacity: 0;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  -webkit-transform: translate3d(0, 20px, 0);
  transform: translate3d(0, 20px, 0);
}

@font-face {
  font-family: archive;
  src: url("Archive.otf");
}

@media (max-width: 767px) {
  .calltoaction {
    text-align: center;
  }
}

@media (max-width: 480px) {
  .intro h1 {
    font-size: 25px;
  }

  .start {
    font-size: 14px;
  }

  h1 {
    font-size: 34px;
    letter-spacing: 3px;
  }

  h2 {
    font-size: 24px;
    margin: 0 0 30px;
    letter-spacing: 3px;
  }

  .info ul .line-btn .buy,
  .info ul .line-btn .detailed {
    margin: 0;
  }

  #certificate .pictures {
    width: 230px;
  }
}